const remRatio = 75

/**
 * px è½¬ rem
 * @example
 * 1. 10remè½¬æ¢
 * 2. è°ç¨: rem2px(10)
 * 3. ç»æ: 100
 *
 * 1. 10remè½¬æ¢
 * 2. è°ç¨: rem2px('10rem')
 * 3. ç»æ: 100px
 *
 * @param value
 * @return {number|string}
 */
export function rem2px(value) {
  let val = parseFloat(value) * remRatio
  if (typeof value === 'string' && value.match(/rem$/)) {
    val += 'px'
  }
  return val
}

/**
 * px è½¬ rem
 * @example
 * 1. 100pxè½¬æ¢
 * 2. è°ç¨: px2rem(100)
 * 3. ç»æ: 10
 *
 * 1. 100pxè½¬æ¢
 * 2. è°ç¨: px2rem('100px')
 * 3. ç»æ: 10rem
 *
 * @param value
 * @return {number|string}
 */
export function px2rem(value) {
  let val = parseFloat(value) / remRatio
  if (typeof value === 'string' && value.match(/px$/)) {
    val += 'rem'
  }
  return val
}
