const throttle = function(fn, delay, atLeast) {
  let timer = null;
  let previous = null;

  return function() {
    let now = +new Date();

    if (!previous) previous = now;

    if (now - previous > atLeast) {
      fn();
      // éç½®ä¸ä¸æ¬¡å¼å§æ¶é´ä¸ºæ¬æ¬¡ç»ææ¶é´
      previous = now;
    } else {
      clearTimeout(timer);
      timer = setTimeout(function() {
        fn();
      }, delay);
    }
  };
};

export default throttle;
